"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { LogOut, Clock, CheckCircle, AlertCircle, FileText } from "lucide-react";
import { getAuthJSON, postAuthJSON } from "@/utils/api";
import { useToast } from "@/components/ui/toast";
import { useRouter } from "next/navigation";
import Image from "next/image";
import logoImage from "@/public/mesoblogo.jpeg";
import {
  SidebarProvider,
  SidebarInset,
  SidebarTrigger,
  SidebarHeader,
  Sidebar,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
} from "@/components/ui/sidebar";

function OwnerKYCSidebar() {
  const router = useRouter();
  const { show } = useToast();

  const handleSignOut = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('access_token');
    router.push('/');
    show({ title: "Success", description: "Signed out successfully", variant: "success" });
  };

  return (
    <Sidebar className="bg-gray-900">
      <SidebarHeader className="p-6 border-b border-gray-800">
        <div className="flex justify-center">
          <Image
            height={80}
            width={80}
            src={logoImage}
            alt="Mesob Logo"
            className="rounded"
          />
        </div>
      </SidebarHeader>
      <SidebarContent className="p-4">
        <SidebarMenu>
          <SidebarMenuItem>
            <SidebarMenuButton onClick={handleSignOut} className="text-white hover:text-white hover:bg-gray-800">
              <LogOut className="h-4 w-4" />
              <span>Sign Out</span>
            </SidebarMenuButton>
          </SidebarMenuItem>
        </SidebarMenu>
      </SidebarContent>
    </Sidebar>
  );
}

export default function OwnerKYCPage() {
  const router = useRouter();
  const { show } = useToast();
  const [kycData, setKycData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [formData, setFormData] = useState({
    pharmacy_name: "",
    pharmacy_address: "",
    owner_phone: "",
    id_number: "",
    tin_number: "",
    notes: "",
  });
  const [licenseFile, setLicenseFile] = useState<File | null>(null);
  const [existingLicense, setExistingLicense] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadKYCData();
  }, []);

  const loadKYCData = async () => {
    try {
      setLoading(true);
      const me = await getAuthJSON("/api/v1/auth/me");
      const tenantId = me.tenant_id;
      
      if (!tenantId) {
        show({ title: "Error", description: "No tenant ID found", variant: "destructive" });
        return;
      }

      // Check if already approved, redirect to payment or dashboard
      if (me.kyc_status === "approved") {
        if (me.subscription_status !== "active") {
          router.push("/dashboard/payment");
        } else {
          router.push("/dashboard/owner");
        }
        return;
      }

      const data = await getAuthJSON(`/api/v1/kyc/${tenantId}/status`);
      setKycData(data);
      
      setFormData({
        pharmacy_name: data.pharmacy_name || "",
        pharmacy_address: data.pharmacy_address || "",
        owner_phone: data.owner_phone || "",
        id_number: data.id_number || "",
        tin_number: data.pharmacy_license_number || "",
        notes: data.notes || "",
      });
      setExistingLicense(data.license_document_name || data.pharmacy_license_document_path || "");
    } catch (error) {
      console.error("Failed to load KYC data:", error);
      setKycData({ status: "not_submitted" });
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!existingLicense && !licenseFile) {
      show({ title: "Error", description: "Business license document is required", variant: "destructive" });
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      const me = await getAuthJSON("/api/v1/auth/me");
      const tenantId = me.tenant_id;
      
      await postAuthJSON(`/api/v1/kyc/${tenantId}/update`, formData);
      show({ title: "Success", description: "KYC information updated successfully. Redirecting...", variant: "success" });
      setTimeout(() => router.push("/dashboard/owner"), 1500);
    } catch (error) {
      console.error("Submit error:", error);
      show({ title: "Error", description: error?.message || "Failed to update KYC information", variant: "destructive" });
    } finally {
      setIsSubmitting(false);
    }
  };

  const getStatusBadge = () => {
    switch (kycData?.status) {
      case "pending":
        return <Badge className="bg-yellow-100 text-yellow-800 border border-yellow-300"><Clock className="h-3 w-3 mr-1" />Pending Review</Badge>;
      case "approved":
        return <Badge className="bg-green-100 text-green-800 border border-green-300"><CheckCircle className="h-3 w-3 mr-1" />Approved</Badge>;
      case "rejected":
        return <Badge className="bg-red-100 text-red-800 border border-red-300"><AlertCircle className="h-3 w-3 mr-1" />Rejected</Badge>;
      default:
        return <Badge className="bg-gray-100 text-gray-800 border border-gray-300">Not Submitted</Badge>;
    }
  };

  if (loading) {
    return (
      <div>
        <SidebarProvider>
          <OwnerKYCSidebar />
          <SidebarInset className="p-3 bg-white">
            <div className="flex items-center justify-center h-screen">
              <div className="text-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
                <p>Loading KYC data...</p>
              </div>
            </div>
          </SidebarInset>
        </SidebarProvider>
      </div>
    );
  }

  return (
    <div>
      <SidebarProvider>
        <OwnerKYCSidebar />
        <SidebarInset className="bg-white min-h-screen">
          <div className="bg-white px-6 py-4 flex items-center gap-3">
            <SidebarTrigger />
            <h1 className="text-xl font-semibold text-gray-900">KYC Verification</h1>
          </div>
          <div className="p-6 max-w-4xl mx-auto">
            <div className="mb-6">
              <div className="flex items-center justify-between mb-4">
                <div>
                  <p className="text-gray-600 text-sm">Complete your verification to start managing your business</p>
                </div>
                {getStatusBadge()}
              </div>
            </div>

            {kycData?.status === "rejected" && kycData?.admin_notes && (
              <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
                <div className="flex items-start gap-3">
                  <AlertCircle className="h-5 w-5 text-red-600 mt-0.5" />
                  <div>
                    <h3 className="font-semibold text-red-900 mb-1">Application Rejected</h3>
                    <p className="text-red-700 text-sm">{kycData.admin_notes}</p>
                  </div>
                </div>
              </div>
            )}

            <Card className="border border-gray-200">
              <CardHeader className="border-b bg-gray-50">
                <CardTitle className="flex items-center gap-2 text-gray-900">
                  <FileText className="h-5 w-5" />
                  {kycData?.status === "not_submitted" ? "Submit Verification Documents" : "Edit Verification Documents"}
                </CardTitle>
              </CardHeader>
              <CardContent className="p-6">
                <form onSubmit={handleSubmit} className="space-y-6">
                  <div>
                    <Label htmlFor="pharmacy_name" className="text-gray-900 font-semibold">Business Name *</Label>
                    <Input
                      id="pharmacy_name"
                      value={formData.pharmacy_name}
                      onChange={(e) => setFormData(prev => ({ ...prev, pharmacy_name: e.target.value }))}
                      placeholder="Enter your registered business name"
                      className="mt-2"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="pharmacy_address" className="text-gray-900 font-semibold">Business Address *</Label>
                    <Input
                      id="pharmacy_address"
                      value={formData.pharmacy_address}
                      onChange={(e) => setFormData(prev => ({ ...prev, pharmacy_address: e.target.value }))}
                      placeholder="Business location"
                      className="mt-2"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="owner_phone" className="text-gray-900 font-semibold">Phone Number *</Label>
                    <Input
                      id="owner_phone"
                      value={formData.owner_phone}
                      onChange={(e) => setFormData(prev => ({ ...prev, owner_phone: e.target.value }))}
                      placeholder="+251..."
                      className="mt-2"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="id_number" className="text-gray-900 font-semibold">National ID Number *</Label>
                    <Input
                      id="id_number"
                      value={formData.id_number}
                      onChange={(e) => setFormData(prev => ({ ...prev, id_number: e.target.value }))}
                      placeholder="Government-issued identification number"
                      className="mt-2"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="tin_number" className="text-gray-900 font-semibold">TIN Number *</Label>
                    <Input
                      id="tin_number"
                      value={formData.tin_number}
                      onChange={(e) => setFormData(prev => ({ ...prev, tin_number: e.target.value }))}
                      placeholder="Tax Identification Number"
                      className="mt-2"
                      required
                    />
                  </div>

                  <div>
                    <Label htmlFor="notes" className="text-gray-900 font-semibold">Additional Notes</Label>
                    <Input
                      id="notes"
                      value={formData.notes}
                      onChange={(e) => setFormData(prev => ({ ...prev, notes: e.target.value }))}
                      placeholder="Any additional information"
                      className="mt-2"
                    />
                  </div>

                  <div>
                    <Label className="text-gray-900 font-semibold mb-2 block">
                      Business License Document *
                    </Label>
                    {existingLicense && (
                      <div className="mb-3 p-3 bg-blue-50 border border-blue-200 rounded-lg">
                        <p className="text-sm text-blue-900 font-medium mb-1">Current Document:</p>
                        <p className="text-xs text-blue-700 break-all">{existingLicense}</p>
                      </div>
                    )}
                    <div className="relative">
                      <input
                        type="file"
                        id="license-upload"
                        accept=".pdf,.jpg,.jpeg,.png"
                        onChange={(e) => setLicenseFile(e.target.files?.[0] || null)}
                        className="hidden"
                      />
                      <label
                        htmlFor="license-upload"
                        className="flex items-center justify-between px-4 py-3 border border-gray-300 rounded-lg cursor-pointer transition hover:border-gray-400 hover:bg-gray-50"
                      >
                        <div className="flex items-center gap-3 flex-1 min-w-0">
                          {licenseFile && (
                            <div className="w-10 h-10 rounded bg-gray-100 flex items-center justify-center flex-shrink-0">
                              <FileText className="w-5 h-5 text-gray-600" />
                            </div>
                          )}
                          <div className="flex-1">
                            <p className="text-sm font-medium text-gray-900">
                              {licenseFile ? licenseFile.name : existingLicense ? "Upload new document (optional)" : "No file selected"}
                            </p>
                            <p className="text-xs text-gray-500 mt-0.5">
                              {existingLicense ? "Leave empty to keep current document" : "Upload business license document"}
                            </p>
                          </div>
                        </div>
                        <div className="px-4 py-2 bg-emerald-600 text-white text-sm font-semibold rounded hover:bg-emerald-700 transition ml-3">
                          BROWSE
                        </div>
                      </label>
                    </div>
                  </div>

                  <Button 
                    type="submit" 
                    disabled={isSubmitting} 
                    className="w-full bg-emerald-600 hover:bg-emerald-700 text-white py-3 font-semibold"
                  >
                    {isSubmitting ? "Updating..." : kycData?.status === "not_submitted" ? "Submit KYC Application" : "Update KYC Information"}
                  </Button>
                </form>
              </CardContent>
            </Card>

            <div className="mt-6 rounded border border-amber-200 bg-amber-50 p-4 text-sm text-amber-800">
              After submitting your KYC information, our admin team will review it. Once approved, you'll proceed to payment verification.
            </div>
          </div>
        </SidebarInset>
      </SidebarProvider>
    </div>
  );
}
